<template>
  <v-ons-page>
    <div ref="gameStage">
      <v-ons-row v-for="(level, i) in levels" :key="i" :class="i === 0 ? 'pt-5' : ''">
        <v-ons-col
          v-for="(le, j) in level" :key="j"
          class="pa-1"
          v-touch:tap="goPlay"
        >
          <!-- <router-link v-touch:tap="touchHandler" :to="{name: 'Play', params: { stage: stage, id : i}}" ><v-ons-button -->
          <v-ons-button
            class="stage-num w-100 pa-1"
            modifier="outline"
            :class="clearList.indexOf(String(le)) !== -1 ? 'clear' : ''"
          >{{le}}</v-ons-button>
        </v-ons-col>
      </v-ons-row>
    </div>
  </v-ons-page>
</template>

<style scoped>
.clear {
  background: #3880ff !important;
  color: white;
}
.stage-num {
  font-size: 6vw;
  border-radius: 20px;
  padding: 15px 10px !important;
  text-align: center;
  border: 1px solid #3880ff !important;
  text-overflow: clip;
}
@media (max-width: 321px) {
  .stage-num {
  padding: 10px !important;
  }
}
</style>

<script>
export default {
  props: ["stage"],
  name: "level-stage",
  data() {
    return {
      levels: [],
      width: 0,
      favoriteList: [],
      clearList: []
    };
  },
  created() {
    this.gameSetting();
  },
  mounted() {
    for (let i = 0; i <= 20; i++) {
      let t = [];
      for (let j = 0; j < 5; j++) {
        t.push(i * 5 + j + 1);
      }
      this.levels.push(t);
    }
  },
  methods: {
    goPlay(e) {
      let id = Number(e.target.innerText);
      this.$router.push({
        name: "Play",
        params: { stage: this.stage, id: id }
      });
    },
    gameSetting() {
      let key = `${this.stage}-clear`;
      let result = localStorage[key];
      if (result === undefined) {
        localStorage[key] = [];
      } else {
        this.clearList = result.split(",");
      }
    }
  }
};
</script>


